<template>
  <q-header elevated class="bg-teal-10 text-white" height-hint="98">
    <q-toolbar>
      <q-toolbar-title>
        <q-avatar>
          <q-img src="../assets/logo.png" />
        </q-avatar>
        Network Management
      </q-toolbar-title>
      <q-tabs align="left">
        <q-route-tab v-if="isLoggedIn" to="/users" label="Users List" />
      </q-tabs>
      <q-tabs align="left">
        <q-route-tab v-if="isLoggedIn" @click="onLogout()" label="Logout" />
      </q-tabs>
    </q-toolbar>
  </q-header>
</template>

<script>
export default {
  data() {
    return {
      isLoggedIn: false
    }
  },
  methods: {
    async onLogout() {
      this.isLoggedIn = false;
      await localStorage.removeItem('token');
      await localStorage.removeItem('user');
      this.$store.state.token = null;
      this.$store.state.user = null;
      await this.$q.notify({
        message: `Log out successfully.`,
        color: 'red-5'
      })
      await this.$router.push('/login');
    },
  },
  created() {
    this.$router.beforeEach((to, from, next) => {
      const user = localStorage.getItem('user');
      if (user !== 'null' && user !== null) { this.isLoggedIn = true } else { this.isLoggedIn = false }
      next();
    });
  }
};
</script>

